.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

@import url('https://fonts.googleapis.com/css2?family=Lora&family=Montserrat&display=swap');

/* 
FONTS
font-family: 'Lora', serif;
font-family: 'Montserrat', sans-seif; 
ICONS
<i class="fas fa-minus"></i>
<i class="fas fa-plus"></i>
*/

html {
  font-size: 62.5%;
}

body {
  font-family: 'Lora', serif;
  background: #bdc3c7; /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #d3cccc, #1a4a79); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #868686, #1a4a79); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.background-animation-1 {
  position: fixed;
  background-image: url(./assets/background-effect.png);
  background-size: cover;
  width: 1200px;
  height: 1200px;
  left: 80%;
  bottom: 5%;
  z-index: -1;
  animation: spin 50s linear infinite;
}

.main {
  min-height: 90vh;
}

@keyframes spin { 
  from { 
      transform: rotate(0deg); 
  } to { 
      transform: rotate(360deg); 
  }
}
h1 {
  font-size: 5rem;
  font-weight: bold;
}

h2 {
  font-size: 3rem;
  font-weight: bold;
  line-height: 3rem;
}

h3 {
  font-size: 2.1rem;
  font-weight: bold;
}

h4 {
  font-size: 2.5rem;
  font-weight: bold;
}

p {
  font-size: 1.6rem;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  width: 90%;
}

@media (max-width: 1000px) {
  .wrapper {
    width: 95%;
  }
}

@media (max-width: 850px) {
  .wrapper {
    width: 100%;
  }
}

@media (max-width: 400px) {
  h1 {
    font-size: 4rem;
  }
}